import React from 'react'
import { Outlet, useNavigate, Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import { Logout } from '../../services/account-service';
import { CurrentUser } from '../../services/account-service';
import { Auth } from '../../services/account-service';
import { Loading } from '../../components/loading';
import { NotificationManager } from 'react-notifications';


class Layout extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            loadedAuth: false,
            menuOpenSataus: false,
            isActive: false,
            isAdmin: false,
            isSuperAdmin: false,
        }
    }

    async componentDidMount() {
        const user = await CurrentUser();
        if (!user) this.props.navigate('/login');
        await Auth();
        await this.setState({
            isActive: localStorage.getItem("isActive") === 'true',
            isAdmin: localStorage.getItem("isAdmin") === 'true',
            isSuperAdmin: localStorage.getItem("isSuperAdmin") === 'true',
            loadedAuth: true,
        })
    }

    MenuClick = () => {
        this.setState({ menuOpenSataus: !this.state.menuOpenSataus })
    }

    Logout = async () => {
        await Logout(
            () => {
                NotificationManager.success('成功', '登出',3000);
                this.props.navigate('/login');
            },
            () => {
                NotificationManager.error('錯誤', '登出',3000);
            }
        );
    }

    render() {
        if (!this.state.loadedAuth) return <Loading />

        if (!this.state.isActive && !this.state.isAdmin && !this.state.isSuperAdmin) {
            return (
                <div className="p-3">
                    <div className="mx-auto shadow rounded mt-3 mb-3" style={{ maxWidth: '600px', width: '100%', padding: '40px 30px' }}>
                        <span>目前沒有權限<br /><br />請聯繫管理者賦予權限</span>
                    </div>
                </div>
            );
        }


        let Menu = <i className="bi bi-menu-up"></i>;

        if (this.state.menuOpenSataus) Menu = <i className="bi bi-arrow-return-left"></i>;

        let AdminItem = [];

        if (this.state.isAdmin || this.state.isSuperAdmin) {
            AdminItem.push(<Link key={"create"} to={"./create"}>
                <Button className={`btn-circle position-fixed end-0 m-4 transition-primary btn-success ${this.state.menuOpenSataus ? "shadow" : ""}`} style={{ bottom: `${this.state.menuOpenSataus ? "260px" : "0"}`, zIndex: "50" }}>
                    <i className="bi bi-plus-lg"></i>
                </Button>
            </Link>);
            AdminItem.push(<Link key={"manage"} to={"./manage"}>
                <Button className={`btn-circle position-fixed end-0 m-4 transition-primary btn-success ${this.state.menuOpenSataus ? "shadow" : ""}`} style={{ bottom: `${this.state.menuOpenSataus ? "195px" : "0"}`, zIndex: "50" }}>
                    <i className="bi bi-gear"></i>
                </Button>
            </Link>);
        }

        return (
            <div>
                {AdminItem}
                <Link to={"./join"}>
                    <Button className={`btn-circle position-fixed end-0 m-4 transition-primary btn-success ${this.state.menuOpenSataus ? "shadow" : ""}`} style={{ bottom: `${this.state.menuOpenSataus ? "130px" : "0"}`, zIndex: "50" }}>
                        <i className="bi bi-house-fill"></i>
                    </Button>
                </Link>
                <Button className={`btn-circle position-fixed end-0 m-4 transition-primary btn-success ${this.state.menuOpenSataus ? "shadow" : ""}`} style={{ bottom: `${this.state.menuOpenSataus ? "65px" : "0"}`, zIndex: "50" }} onClick={this.Logout}>
                    <i className="bi bi-door-closed"></i>
                </Button>
                <Button className={`btn-circle position-fixed end-0 m-4 transition-primary shadow ${this.state.menuOpenSataus ? "btn-danger" : "btn-success"}`} style={{ bottom: "0", zIndex: "60" }} onClick={this.MenuClick}>
                    {Menu}
                </Button>
                <div>
                    <Outlet />
                </div>
            </div>
        )
    }
}

export default function LayoutNavigate(props) {
    const navigate = useNavigate();

    return <Layout {...props} navigate={navigate} />;
}